@import url(https://fonts.googleapis.com/css?family=Nunito:400,700&display=swap);
/* colors */
.latest {
  padding: 2em 0 7em 0;
  text-align: center; }
  .latest__title {
    margin-bottom: 0;
    font-weight: 500;
    text-transform: capitalize;
    padding: 0.5em; }
    .latest__title {
      font-size: 1.424rem; }
      @media screen and (min-width: 20rem) {
        .latest__title {
          font-size: calc(1.44551vw + 1.1349rem); } }
      @media screen and (min-width: 85.375rem) {
        .latest__title {
          font-size: 2.369rem; } }
  .latest__line {
    position: relative; }
    .latest__line::before {
      content: "";
      display: block;
      position: absolute;
      top: 0;
      right: 0;
      left: 0;
      width: 10px;
      height: 10px;
      margin: 0 auto;
      color: white;
      background: #1292B4;
      border-radius: 50%;
      width: 60%;
      height: 3px; }
  .latest__posts {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 32px; }
  .latest__card {
    position: relative;
    background-color: #fff;
    background-clip: border-box;
    border: 1px solid rgba(0, 0, 0, 0.125);
    border-radius: 0.25em;
    width: 288px;
    height: 288px;
    margin-right: 16px;
    cursor: pointer; }
    .latest__card:hover {
      opacity: 0.7; }
      .latest__card:hover > a {
        color: #1292B4; }
    .latest__card > a {
      text-decoration: none;
      color: #444; }
    .latest__card__img {
      height: 192px;
      width: 100%;
      border-top-left-radius: calc(0.25rem - 1px);
      border-top-right-radius: calc(0.25em - 1px);
      display: block; }
    .latest__card__body {
      padding: 0em 0.9em; }
    .latest__card__text {
      font-size: 1rem;
      line-height: 1.5em; }

@media (max-width: 599px) {
  .latest__title {
    font-size: 1.125rem; } }
  @media screen and (max-width: 599px) and (min-width: 20rem) {
    .latest__title {
      font-size: calc(0.31816vw + 1.06137rem); } }
  @media screen and (max-width: 599px) and (min-width: 85.375rem) {
    .latest__title {
      font-size: 1.333rem; } }

@media (max-width: 599px) {
  .latest__posts {
    flex-direction: column; }
  .latest__card {
    margin: 0.5em; } }

@media (min-width: 1800px) {
  .latest__title {
    font-size: 1.602rem; } }
  @media screen and (min-width: 1800px) and (min-width: 20rem) {
    .latest__title {
      font-size: calc(2.37859vw + 1.12628rem); } }
  @media screen and (min-width: 1800px) and (min-width: 85.375rem) {
    .latest__title {
      font-size: 3.157rem; } }

@media (min-width: 1800px) {
  .latest__card {
    width: 448px;
    height: 448px; }
    .latest__card__img {
      height: 320px; }
    .latest__card__text {
      font-size: 1.125rem; } }
    @media screen and (min-width: 1800px) and (min-width: 20rem) {
      .latest__card__text {
        font-size: calc(0.31816vw + 1.06137rem); } }
    @media screen and (min-width: 1800px) and (min-width: 85.375rem) {
      .latest__card__text {
        font-size: 1.333rem; } }

